<template>
    <section>
        <tg-list :IsShowAction="false" 
        :listType="['List']"
         ModuleName="Asset" 
         SubModuleCode="Asset"
         IdentityColumn="AssetRequisitionId" 
         HeaderText="New Product Type Request" 
         :ListData="ProductData"
         :HeaderData="Headers" 
         :widgets="widgets" 
         :callbackfunction="FetchData" 
         :HeaderButtons="listheaderbuttons"
         :TotalRecords="TotalRecord" 
         :SearchFields="leftSearchFields" 
         :RenderRowActionMethod="renderActions"
         :ListDataCallBackFunction="FetchData" 
         :LegendArray="LegendArray" 
         :SortExp="SortExp" 
         :SortBy="SortBy"
          v-on:LeftsearchButtonClick="leftCommonSearch" 
          v-on:PagerButtonClick="pagerMethod"
          v-on:HeaderButtonClick="commonHeaderButtonClick" 
          v-on:ActionButtonClick="actionButtonClick"
         v-on:SortdataButtonClick="sortdata">

        </tg-list>
        <RelationPopup v-if=IsShowRelation></RelationPopup>
    </section>
</template>

<script>
import Schema from "../AssetRequisition/NewAssetRequisitionListingSchema"
import DataService from '../../service/DataService'
import RelationPopup from '../../components/Modal/AddRelationPopup.vue'
export default {
    components: {
        RelationPopup
    },
    data() {
        return {
            listheaderbuttons: Schema.buttons,
            leftSearchFields: Schema.leftsearchSchema,
            listActions: Schema.listActions,
            LegendArray: [],
            ProductData: [],
            PageSize: 10,
            PageNumber: 1,
            PageSizeOption: null,
            StatusListForListing: [],
            Headers: [{
                "COLUMN_NAME": "ProductName",
                "DATA_TYPE": "image",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('Product Name'),
                "DISPLAY_ORDER": 1,

            },
            {
                "COLUMN_NAME":  "AssetTypeId",
                "DATA_TYPE": "text",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('Product Type'),
                "DISPLAY_ORDER": 2
            },
            {
                "COLUMN_NAME":  "Quantity",
                "DATA_TYPE": "text",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('Requested Quantity'),
                "DISPLAY_ORDER": 3
            },
            {
                "COLUMN_NAME": "Description",
                "DATA_TYPE": "text",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME":this.$t('Description') ,
                "DISPLAY_ORDER": 4,
                settings: {
                    clickEvent: this.clickEventOfColumn,
                    formatter: this.columnDataFormatter,
                }


            },
            {
                "COLUMN_NAME": "CreatedAt",
                "DATA_TYPE": "text",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME":this.$t('CreatedAt'),
                "DISPLAY_ORDER": 5,
               
            },
            {
                "COLUMN_NAME": "CreatedBy",
                "DATA_TYPE": "text",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME":this.$t('Created By') ,
                "DISPLAY_ORDER": 6,
                
            },

            {
                "COLUMN_NAME": "View",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('View') ,
                "DISPLAY_ORDER": 7,
                settings: {
                    isInSlot: true
                }
            }


            ],
            noRecord: true,
            TotalRecord: 0,
            CurrentPage: 1,
            TotalPages: 0,
            LocationId: '',
            SortBy: '',
            SortExp: '',
            PageNumber: 1,
            isPaged: 1,
            PageSize: 10,            
            searchCondition: "",
            widgets: [

            ],
            legendProgress: [],
            IsShowRelation: false,
        }
    },
    created() {
        var vm = this;
        vm.FetchData();
        // Schema.bindleftCommonSearchdropdown(vueObj, "Asset", "Location"); 
        // Schema.bindleftCommonSearchdropdown(vueObj, "Asset", "Tag");  
        // Schema.bindleftCommonSearchdropdown(vueObj, "Asset", "Status");           
    },

    methods: {
        columnDataFormatter: function (row, column) {
            return row[column.COLUMN_NAME];
        },
        
        FetchData:function(){
            debugger
            var vm = this;
            vm.isLoading = true;            
            var params = `pageSize=${vm.PageSize}&pageNumber=${vm.PageNumber}&sortBy=${vm.SortBy}&sortExp=${this.SortExp}&${vm.searchCondition}`;
            DataService.NewAssetRequisitionListing(params).then(response=>{
                debugger;  

                if (response.data != null) {
                    debugger
                    if (response.data.length > 0) {
                        vm.ProductData = response.data;
                        vm.TotalRecord = response.data[0].Total;
                        vm.TotalPage = Math.ceil(vm.TotalRecord / vm.PageSize);
                        vm.CurrentPage = vm.PageNumber;
                        vm.noRecord = false;
                        vm.isLoading = false;
                    }
                    else {
                        vm.noRecord = true;
                        vm.ProductData = [];
                        vm.TotalRecord = 0
                        vm.isLoading = false;
                    }
                }
                setTimeout(function () {
                    vm.ResponsiveDataTable('tablelistingdata')
                }, 500);   
            });
        },
               
        clickEventOfColumn: function (row, column) {

        },

        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchConditionJson;
            }
            else {
                this.searchCondition = "";
            }
            this.FetchData();
        },
        currentPageMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        GetPageSizeOption: function () {
            var list = [];
            var defaultPageSize = 10;
            for (var i = defaultPageSize; i < defaultPageSize + 95; i += 5) {
                list.push(i);
            }
            this.PageSizeOption = list;
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },

        commonHeaderButtonClick(event) {
            switch (event.callbackfunction) {
                case "RequestNewProductType":
                    debugger;
                    this.RequestNewProductType();
                    break;

            }
        },

        RequestNewProductType() {
            debugger;
            this.$router.push({ path: '/AssetRequisition/AddNewRequisition' })

        },


        AddRelationShipPopup() {

        },
        CloseAddRelationShipPopup() {

        },
        actionButtonClick() {

        },
        sortdata: function (s) {

        },

        renderActions(row) {

        },

    },
    updated: function () {
        this.BindActionButtonEvent();
    },
}

</script>